



























































//获取cfg.js中的url配置信息
import cfg from '../../cfg.js';
import request from '../../utils/req.js';
import util from '../../utils/util.js';

//获取应用实例
import all from '../../common/global.js';


let selectedStyle = ""; // 当前选择的风格
let selectedType = ""; // 当前选择的类型
let selectedPriceRange = ""; // 当前选择的价位
let pictures = []; // 商品图片列表

export default {
	data() {
		return {
			inputShowed: false,
			inputVal: "",
			commodity: [],
			pageNum: 0,
			style: ['全部', '优雅', '古典', '自然', '前卫', '少女', '少年', '浪漫', '戏剧', '罗曼', '异域'],
			ctype: ['全部', '外套', '上装', '下装', '连衣裙', '包', '鞋', '饰品'],
			priceRange: ['全部', '200元以下', '200~499元', '500~999元', '1000~1499元', '1500~1999元', '2000元以上']
		}
	},
	onLoad: function(options) {
		this.getList();
	},
	  //上拉分页
	onReachBottom: function () {
	  //上拉分页,pageNum+1，然后调用分页函数getLsit()
	  var that = this;
	  that.pageNum= ++that.pageNum
	  uni.showToast({
	    icon: 'loading',
	    duration: 500,
	  })
	  that.getList();
	  that. title= "数据加载完毕";
	
	},
	
	//下拉刷新
	onPullDownRefresh: function () {
	  var that = this;
	  that.pageNum= 0,
	    that.commodity=[];
	  that.getList();
	  uni.stopPullDownRefresh();
	},
	 onReady: function () {
	  uni.setNavigationBarTitle({
	    title: '商家平台',
	  })
	},
	methods: {
		dataLeftCompleting(bits, identifier, value) {
			value = Array(bits + 1).join(identifier) + value;
			return value.slice(-bits);
		},

		getList() { //分页获取商品信息
			var that = this;
			request.req({
						url: cfg.qjUrl + 'commodity/list',
						method: 'get',
						data: {
							'styles': selectedStyle,
							'type': selectedType,
							'priceRange': selectedPriceRange,
							'pageNum': that.pageNum
						},
						success: (res) => {
							console.log("djjsds", res)
							if (res.statusCode == 200) {

								//如果商品返回的数据为空
								if (res.data.msg.length == 0) {
									uni.showToast({
										title: "没有更多的数据了...",
										icon: 'fail',
										duration: 1000
									});
								} else {
									let commodity = res.data.msg;
									//将图片路径转换为全路径
									for (let i in commodity) {
										let detail = encodeURIComponent(commodity[i].detail)
										commodity[i].detail = detail;
										commodity[i].id = "ME" + this.dataLeftCompleting(11, "0", commodity[i].id)
										if (commodity[i].picture) {
											commodity[i].picture = cfg.qiniuDomain + commodity[i].picture
										} else {
											commodity[i].picture = 'https://qj.foxcodes.cn/images/logo.jpg'
										}
										this.commodity.push(commodity[i]);
									}
										this.commodity= this.commodity;		
								}
							} else {
								console.log("网络访问错误！检查服务器是否正常！")
							}

						},
						fail: function(res) {
							console.log('网络访问失败！')
						}
					})
				},
				showInput () {
				  this.inputShowed= true
				},
				hideInput() {
				  this.inputVal= "";
				    this.inputShowed= false
				},
				clearInput () {
				  this.inputVal= "";
				},
				inputTyping(e) {
				  console.log(e.detail.value);
				  this.inputVal= e.detail.value;
				},
				previewImage(e) {
				  console.log(e.currentTarget);
				  console.log(pictures);
				  uni.previewImage({
				    current: e.currentTarget.id, // 当前显示图片的http链接
				    urls: pictures // 需要预览的图片http链接列表
				  })
				},
				showDetail(e) {
				  all.globalData.costumes = [{
				    "url": this.commodity[e.currentTarget.id].detail
				  }];
				  uni.navigateTo({
				    url: '../showCostume/showCostume?url=' + this.commodity[e.currentTarget.id].detail + "&name=" + this.commodity[e.currentTarget.id].name + "&picture=" + this.commodity[e.currentTarget.id].picture
				  })
				},
				contactHandle (e) {
				
				},
				
				bindPickerChange (e) {
					
				  let column = e.currentTarget.dataset.column;
				  let idx = e.detail.value;
				  console.log(column, idx);
					
				  console.log('picker发送选择改变，携带值为', idx, this[column][idx]);
				  let v = this[column][idx];
				  if (v == "全部") {
				    v = "";
				  }
				  switch (column) {
				    case "style":
				      selectedStyle = v;
				      break;
				    case "ctype":
				      selectedType = v;
				      break;
				    case "priceRange":
				      selectedPriceRange = v;
				      break;
				  }
				  console.log("selectedStyle:", selectedStyle, "selectedType:", selectedType, "selectedPriceRange:", selectedPriceRange);
				  // 取得选定商品信息
				  requset.req({
				    url: cfg.qjUrl + 'commodity/list',
				    method: 'get',
				    data: {
				      'styles': selectedStyle,
				      'type': selectedType,
				      'priceRange': selectedPriceRange,
				      'pageNum':this.pageNum
				    },
				    success: (res) => {
				      if (res.statusCode == 200) {
				        console.log('返回商品列表，req result：', res);
				        
				        let commodity = res.data.msg;
				        //将图片路径转换为全路径
				        for (let i in commodity) {
				          commodity[i].id = "ME" + this.dataLeftCompleting(11, "0", commodity[i].id)
				          if (commodity[i].picture) {
				            commodity[i].picture = cfg.qiniuDomain + commodity[i].picture
				          } else {
				            commodity[i].picture = 'https://qj.foxcodes.cn/images/logo.jpg'
				          }
				          pictures.push(commodity[i].picture); // 将商品图片url保存到pictures数组中
				        }
				        console.log("comodity排序前: ", commodity);
				        commodity.sort(util.compare("priority")); // 将商品按prioriy降序排序
				        console.log("comodity排序后: ", commodity);
				        this.commodity= commodity;
				      } else {
				        console.log("网络访问错误！检查服务器是否正常！")
				      }
				    },
				    fail: function (res) {
				      console.log('网络访问失败！')
				    }
				  })
				
				  this.index= e.detail.value;
				}

	}
	}
